.RangeContainer {
    margin: 0 10px;
    margin-bottom: 12px;

    .RangeTitle {
        font-size: 12px;
        color: #fff;
        margin: 0;
        margin-bottom: 5px;
    }

    .Range {
        appearance: none;
        -webkit-appearance: none;
        height: 27px;
        background-color: #353535;
        border-radius: 999px;
        width: 100%;
        overflow: hidden;

    
        &::-webkit-slider-thumb{
            appearance: none;
            -webkit-appearance: none;
            width: 30px;
            height: 27px;
            background: #83aaff;
            box-shadow: 0 0 0 0 #83aaff;
            border-radius: 999px;
            border: 0;
            box-shadow: -210px 0 0 195px #83aaff;
    
            &:hover {
                background: #97b8ff;
                box-shadow: -210px 0 0 195px #97b8ff;
            }
        }
    }
}