.Header{
    position: fixed;
    top: -40px;
    z-index:9999;
    width: 100%;
    background:#00000055;
    display: flex;
    justify-content: space-between;
    font-size: 11px;
    padding: 5px 10px;
    transition: all .2s;

    &.active {
        top: 0;
    }

    * {
        margin: 0;
    }

    .Header-item {
        padding: 7px 15px;
        color: #fff;
        background:transparent;
        width: fit-content;
        border-radius:999px;
        transition: ease .05s;
    }

    .Header-item:hover{
        background: #ffffff1d;
    }

    .Task i {
        margin: 0 5px;
    }
}