.Window {
    position: absolute;
    width: 800px;
    border-radius: 10px;
    opacity: 0;
    pointer-events: none;

    &.active {
        opacity: 1;
        pointer-events: all;
    }
    
    .TopBar {
        background: #202023da;
        backdrop-filter: blur(15px);
        -webkit-backdrop-filter: blur(15px);
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        padding: 9px 15px;
        display: flex;
        align-items: center;

        .TopBarInteractionContainer {
            display: flex;

            .TopBarInteraction {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: 7px;
                border-radius: 9999px;
                width: 20px;
                height: 20px;
                font-size: 11px;
                color: #d9d9d9;

                &:hover {
                    background: #3b3b3f;
                }

                &.close:hover{
                    background: #ff5252;
                }
            }
        }
        
        .TopBarTitle {
            color:#d9d9d9;
            font-size: 12px;
            margin: 0;
            display: flex;
            justify-content: center;
            width: 100%;
        }
    }
}