* ::-webkit-scrollbar {
    width: 0;
}

* ::-webkit-scrollbar-thumb {
    background: transparent;
}

a {
    text-decoration: none;
}

* {
    box-sizing: border-box;
    font-family: Optimistic Text, -apple-system, ui-sans-serif, system-ui, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
    cursor: default;
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */
}

.font-bold {
    font-weight:700
}

.font-semibold {
    font-weight:600
}

.font-normal {
    font-weight:400
}

.font-medium {
    font-weight:500
}

.Desktop {
    overflow: hidden;
}

.Desktop::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    opacity: 0;
    display: block;
    height: 100%;
    width: 100%;
    z-index: 99999999999;
    background: #ffbf002a;
    pointer-events: none;
    transition: all 2s ease;
}

.Desktop.NightShiftEnabled::before {
    opacity: .8;
}

.error {
    font-size: 15px;
    padding: 50px 25px;
    color: #fff;
    background: #000;
    display: none;
    margin: 0;
    width: 100%;
    height: 100vh;
}

@media screen and (max-width: 540px){
    .Desktop *:not(.error){
        display: none;
    }

    .error {
        display: block;
    }
}