.Panel {
    position: fixed;
    z-index:9999;
    background: #222222;
    color:#fff;
    padding: 8px;
    border-radius: 10px;
    width: 350px;
    display: flex;
    flex-direction: column;
    transition: all ease .15s;
    opacity: 0;
    pointer-events: none;

    &.active {
        opacity: 1;
        pointer-events: all;
    }

    .PanelItem {
        background: transparent;
        padding: 10px 15px;
        font-size: 12px;
        border-radius: 999px;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover {
            background: #353535;
        }
        i {
            margin: 0 5px;
            margin-left: 0;
        }
    }

    .PanelItemInteraction {
        padding: 10px 11px;

        i{
            margin: 0;
        }
    }

    .PanelTop {
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;
    }

    .PanelBottom {
        .PanelItemLarge{
            background: #353535;
            padding: 10px 15px;
            margin-right: 10px;
            margin-bottom: 10px;
            font-size: 14px;
            border-radius: 999px;
            display: inline-block;
            width: 157px;
            transition: ease .1s;

            &:hover {
                background: #444444;
            }
    
            i {
                margin: 0 5px;
                margin-left: 0;
            }

            &.focused{
                background: #83aaff;
                color:#000;

                &:hover {
                    background: #97b8ff
                }
            }
        }
    }
}